.admin-sidebar {
  width: 200px;
  background: #222;
  color: #fff;
  height: 100vh;
  padding-top: 32px;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1001;
  box-shadow: 2px 0 8px rgba(0,0,0,0.05);
}
.admin-sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.admin-sidebar li {
  margin-bottom: 16px;
}
.admin-sidebar a {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  padding: 8px 24px;
  display: block;
  border-radius: 4px;
  transition: background 0.2s;
}
.admin-sidebar li.active a,
.admin-sidebar a:hover {
  background: #1976d2;
  color: #fff;
}
